<script setup lang="ts">
    import { useRoute, useRouter } from "vue-router";
    import { ref } from "vue";

    import SearchRangeType from "@/pages/search/SearchRangeType.vue";
    import SearchGrevfxType from "@/pages/search/SearchGrevfxType.vue";
    import SearchLocalType from "@/pages/search/SearchLocalType.vue";
    import SearchKindType from "@/pages/search/SearchKindType.vue";
    import ClubBox from "./search/ClubBox.vue";
    import TheBannner from "@/components/layout/TheBannner.vue";

    const route = useRoute();

    const tabArr = [
        { label: "근거리 스포츠클럽 검색", value: "range" },
        { label: "지역 스포츠클럽 검색", value: "local" },
        { label: "운영종목 검색", value: "grevfx" },
        { label: "지정 스포츠클럽 검색", value: "gijeung" }
    ];
    const bigTab = ref(route.query.searchType);
    const clubList = ref<CloseRangeResDto | null>(null);
</script>

<template>
    <TheBannner title="스포츠클럽 검색" :page-map="['스포츠클럽 검색', '근거리 스포츠 클럽 검색']" />

    <div class="inner-w top">
        <ul class="tab round" style="margin-bottom: 40px">
            <li v-for="tab in tabArr" :class="{ 'is-active': tab.value === bigTab }" @click="bigTab = tab.value">
                {{ tab.label }}
            </li>
        </ul>
    </div>
    <KeepAlive>
        <SearchRangeType v-if="bigTab === 'range'" @fetch-list="list => (clubList = list)" />
        <SearchLocalType v-else-if="bigTab === 'local'" @fetch-list="list => (clubList = list)" />
        <SearchGrevfxType v-else-if="bigTab === 'grevfx'" @fetch-list="list => (clubList = list)" />
        <SearchKindType v-else-if="bigTab === 'gijeung'" @fetch-list="list => (clubList = list)" />
    </KeepAlive>

    <div class="clubListSec">
        <div class="inner-w">
            <h5 class="title-search">
                총 <span class="font-roboto">{{ clubList?.total }}</span
                >건
            </h5>
            <div class="result-wrap">
                <ClubBox
                    v-for="(item, index) in clubList?.closeRangeResDtoList.slice(0, 10)"
                    :key="index"
                    :clubData="item"
                    :index="index"
                />
            </div>
        </div>
    </div>
</template>

<style scoped lang="scss">
    .tab {
        display: flex;
        width: 100%;

        &-wrap {
            border-bottom: 1px solid #d6dce5;
        }

        li {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #555;
            font-size: 16px;
            cursor: pointer;
        }

        &.round {
            height: 60px;
            border: 1px solid #dfe4e8;
            border-radius: 30px;

            li {
                &:not(:first-child) {
                    position: relative;

                    &::after {
                        content: "";
                        width: 1px;
                        height: 24px;
                        background: #dfe4e8;
                        position: absolute;
                        left: 0;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }

                &.is {
                    &-active {
                        border-radius: 30px;
                        background: #111;
                        color: #fff;
                        font-weight: 500;

                        &::after {
                            display: none;
                        }

                        + li {
                            &::after {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }

    .clubListSec {
        background: #f4f9fb;
        padding: 60px 0 120px;
        .title-search {
            @include font-style(16);
            font-weight: bold;
            margin-bottom: 16px;
            color: #555555;
            .font-roboto {
                font-family: "Roboto", sans-serif;
                color: #0fae6f;
            }
        }
    }
    .top {
        padding-top: 80px;
    }
</style>
