<script setup lang="ts">
    import iconLocation from "@/assets/icon/ic_location.svg";
    import iconPhone from "@/assets/icon/ic_phone.svg";
    import { onMounted } from "vue";

    const props = defineProps<{
        clubData: CloseRangeResDtoList;
        index: number;
    }>();

    const apikey = process.env.VUE_APP_KAKAO_APIKEY;
    const clubHompageUrl = process.env.VUE_APP_WEB_URL + "/club/";

    let map: any;
    // 주소-좌표 변환 객체를 생성합니다
    let geocoder: any;

    declare global {
        interface Window {
            kakao: any;
        }
    }

    let ma: number = 0;
    let la: number = 0;

    onMounted(() => {
        if (window.kakao && window.kakao.maps) {
            initMap();
        } else {
            const script = document.createElement("script");
            /* global kakao */
            script.onload = () => window.kakao.maps.load(initMap);
            script.onerror = err => console.log(err);
            script.src = "//dapi.kakao.com/v2/maps/sdk.js?appkey=" + apikey + "&libraries=services&autoload=false";
            console.log(script.src);
            document.head.appendChild(script);
        }
    });

    function initMap() {
        const container = document.querySelector(`.map${props.index}`);
        const options = {
            // 위도 경도 순으로
            center: new window.kakao.maps.LatLng(33.450701, 126.570667),
            level: 3,
            disableDoubleClickZoom: true
        };

        //지도 객체를 등록합니다.
        //지도 객체는 반응형 관리 대상이 아니므로 initMap에서 선언합니다.
        map = new window.kakao.maps.Map(container, options);
        // 주소-좌표 변환 객체를 생성합니다
        geocoder = new window.kakao.maps.services.Geocoder();

        map.setDraggable(false);
        map.setZoomable(false);

        addressSearch(props.clubData.clubPostAddr);
    }

    /**
     * 주소로 장소 정보 얻기
     */
    function addressSearch(address: string) {
        // 주소로 좌표를 검색합니다
        geocoder.addressSearch(address, function (result: any, status: any) {
            // 정상적으로 검색이 완료됐으면
            if (status === window.kakao.maps.services.Status.OK) {
                var coords = new window.kakao.maps.LatLng(result[0].y, result[0].x);

                ma = coords.Ma;
                la = coords.La;

                // 결과값으로 받은 위치를 마커로 표시합니다
                var marker = new window.kakao.maps.Marker({
                    map: map,
                    position: coords
                });

                // 인포윈도우로 장소에 대한 설명을 표시합니다
                var infowindow = new window.kakao.maps.InfoWindow({
                    content:
                        '<div style="width:150px;text-align:center;padding:6px 0;">' + props.clubData.clubNm + "</div>"
                });
                infowindow.open(map, marker);

                // 지도의 중심을 결과값으로 받은 위치로 이동시킵니다
                map.setCenter(coords);
            }
        });
    }
</script>

<template>
    <div class="result-item">
        <div class="result-item-left">
            <div :class="`map map${index}`">map</div>
        </div>
        <div class="result-item-right">
            <div class="result-item-title d-flex ai-center jc-between">
                <p>
                    <span>{{ clubData.clubNm }}</span> 스포츠클럽
                </p>
                <div class="result-item-btn">
                    <a :href="clubHompageUrl + clubData.clubPostAddr" class="detailBtn round black mr-10">
                        클럽<span>홈페이지</span><VIcon icon="ic_arrow" :style="{ transform: 'rotate(-90deg)' }" />
                    </a>
                    <RouterLink
                        :to="`/FPOD0005C?sprtClubNo=${clubData.sprtClubNo}`"
                        class="detailBtn round black mr-10"
                    >
                        자세히<span>보기</span><VIcon icon="ic_arrow" :style="{ transform: 'rotate(-90deg)' }" />
                    </RouterLink>
                </div>
            </div>
            <div class="result-item-info">
                <ul>
                    <li><img :src="iconLocation" alt="주소" />{{ clubData.clubPostAddr }}</li>
                    <li>
                        <img :src="iconPhone" alt="전화 번호" /><span class="font-roboto">{{ clubData.rpsTelno }}</span>
                    </li>
                </ul>
                <div class="result-item-tag">
                    <div v-for="item in clubData.list">#{{ item.evfxNm }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<style scoped lang="scss">
    .result {
        &-item {
            width: 100%;
            border: 1px solid #dfe4e8;
            background: #fff;
            border-radius: 20px;
            display: flex;
            gap: 40px;
            padding: 30px;
            position: relative;

            & + .result-item {
                margin-top: 42px;
            }

            &-left {
                width: 330px;
                height: 208px;
                background: #333;
                border-radius: 20px;
                flex-shrink: 0;
                overflow: hidden;
                .map {
                    width: 100%;
                    height: 100%;
                }
            }

            &-right {
                width: 100%;
            }

            &-title {
                border-bottom: 2px solid #dfe4e8;
                padding-bottom: 16px;

                p {
                    @include font-style(20);
                    color: #333;
                    font-weight: 400;

                    span {
                        font-weight: bold;
                    }
                }
            }

            &-btn {
                .detailBtn {
                    @include font-style(14);
                    padding: 10px 16px;
                    background-color: #292e41;
                    border-radius: 24px;
                    color: #fff;
                    display: inline-block;
                    i {
                        margin-left: 10px;
                        position: relative;
                        top: -1px;
                    }
                }
            }

            &-info {
                display: flex;
                height: calc(100% - 54px);
                flex-direction: column;
                justify-content: space-between;
                padding-top: 15px;
                gap: 8px;

                li {
                    display: flex;
                    @include font-style(15);
                    color: #333;
                    line-height: 1.87;

                    img {
                        margin-right: 10px;
                    }
                }
            }

            &-tag {
                display: flex;
                flex-wrap: wrap;
                gap: 10px;

                div {
                    display: flex;
                    align-items: center;
                    height: 28px;
                    border-radius: 18px;
                    padding: 0 18px;
                    background: #f7f7f7;
                    font-size: 14px;
                    letter-spacing: -0.42px;
                }
            }
        }
    }
</style>
